<template>
  <v-container class="pt-10">
    <v-card class="client-login" width="500">
      <v-card-title class="card-head">
        西武治療室 高尾 ログイン
      </v-card-title>
      <v-card-text class="card-body">
        <v-form @submit.prevent="handleLogin">
          <div class="form-group">
            <v-text-field
              label="メールアドレス"
              v-model="email"
              type="email"
              placeholder="xxx@example.com"
              outlined
              :error="!!emailError"
              :error-messages="emailError"
              required
            />
          </div>
          <div class="form-group">
            <v-text-field
              label="パスワード"
              v-model="password"
              :type="visiblePass ? 'text' : 'password'"
              outlined
              :error="!!passwordError"
              :error-messages="passwordError"
              placeholder=""
              :append-inner-icon="visiblePass ? 'mdi-eye-off-outline' : 'mdi-eye-outline'"
              @click:append-inner="visiblePass = !visiblePass"
              required
            />
          </div>
          <v-btn type="submit" class="login-button" size="large" append-icon="mdi-login">ログイン</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
export default {
  name: "ClientLogin",
  data() {
    return {
      email: "",
      password: "",
      emailError: null,
      passwordError: null,
      visiblePass: false,
    };
  },
  methods: {
    handleLogin() {
      // バリデーションチェック
      this.emailError = null;
      this.passwordError = null;

      if (!this.validateEmail(this.email)) {
        this.emailError = "正しいメールアドレスを入力してください";
      }

      if (!this.emailError && !this.passwordError) {
        this.$router.push('/user_page'); // ユーザページに遷移
      }
    },
    validateEmail(email) {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    },
  },
};
</script>

